// Create an array of points with curly noise applied
let tubesLength = 0.001

const createCurveArray = (start)=>
{

    let points = []
    let scale = 1.5
    points.push(start)
    let currentPoint = start.clone()

    for(let i=0; i<500; i++)
    {

        let velocity = computeCurl(currentPoint.x / scale, currentPoint.y / scale, currentPoint.z / scale)
        currentPoint.addScaledVector( velocity , tubesLength)
        points.push( currentPoint.clone())

    }
    return points
}

// Material
const material = new THREE.ShaderMaterial({
    vertexShader:vertex,
    fragmentShader:fragment,
    uniforms:{
        uTime:{value:0},
        uLight: {value:new THREE.Vector3(0,0,0)},
        rColor:{value:0},
        gColor:{value:0},
        uColorMultiplier : {value:0}
    }
})

// Create multimple tubes
for( let i=0; i<150; i++ ){
    let position = (Math.random() - 0.5) 

    const path = new THREE.CatmullRomCurve3(createCurveArray(
        new THREE.Vector3( position , position , position))
    )
    const geometry = new THREE.TubeBufferGeometry(path , 600 , 0.005 , 8 , false)
    const mesh = new THREE.Mesh(geometry, material)
    scene.add(mesh)
    meshes.push(mesh)

}

