// Mouse move function
window.addEventListener('mousemove' , (e)=>
{

    mouse.x = ( e.clientX / sizes.width ) * 2 - 1
    mouse.y = - (e.clientY / sizes.height) * 2 + 1
    eMouse.x = e.clientX
    eMouse.y = e.clientY


    // Raycaster
    raycaster.setFromCamera(mouse,camera)
    const intersects = raycaster.intersectObject(planeSource)
    if(intersects.length){
        let point = intersects[0].point
        lightSource.position.copy(point)

        eMouse.x = point.x
        eMouse.y = point.y
    }

    // Objects animation
    for(const mesh of meshes){
        mesh.position.x = mouse.x / 20
        mesh.position.y = mouse.y / 20
    }
    img1.rotation.y = mouse.x
    img2.rotation.y = mouse.x * 0.66
    img3.rotation.y = mouse.x * 0.33

})


/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 100)
camera.position.set(0, 0, 1)
scene.add(camera)


/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
renderer.autoClear = false


// Effect Composer
const effectComposer = new EffectComposer(renderer)
effectComposer.setSize(sizes.width, sizes.height)
effectComposer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

const renderPass = new RenderPass(scene, camera)
effectComposer.addPass(renderPass)

const unrealBloomPass = new UnrealBloomPass()
effectComposer.addPass(unrealBloomPass)



/**
 * Animate
 */
const clock = new THREE.Clock()
const domCursor = document.querySelector('.cursor')

const tick = () =>
{
    const elapsedTime = clock.getElapsedTime()


    // Update cursor element
    tempPosition.copy(eMouse).sub(elasticMouse).multiplyScalar(0.15)
    elasticMouseVelocity.add(tempPosition).multiplyScalar(0.5)
    elasticMouse.add(elasticMouseVelocity)

    lightSource.position.x = elasticMouse.x
    lightSource.position.y = elasticMouse.y

    domCursor.style.transform = `translate(${eMouse.x}px , ${eMouse.y}px)`

    // Update shaders
    settings.time = elapsedTime 
    material.uniforms.uTime.value = settings.time
    material.uniforms.uLight.value = lightSource.position

    planeSource.material.uniforms.uTime.value = settings.time
    planeSource.material.uniforms.uLight.value = lightSource.position
    lightSource.material.uniforms.uTime.value = elapsedTime

    imageMaterial.uniforms.uTime.value = settings.time * 0.1
    
    // Loading shape animation
    loadingShape.rotation.z = elapsedTime * 1.5
    loadingShape.rotation.x = elapsedTime * 1.5

    // Animate points
    aboutMesh.rotation.z = elapsedTime * 0.5
    aboutMesh.material.uniforms.uTime.value = elapsedTime
    portraitMesh.material.uniforms.uTime.value = elapsedTime * 0.1

    // Render
    // renderer.clear()
    // renderer.render(sceneSecond , camera)
    // renderer.clearDepth()
    // renderer.render(scene, camera)
    effectComposer.render()

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()


